"use client";

import { Bell, Settings, User } from "lucide-react";
import { Button } from "@/components/dashboards/warehouse/ui/button";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/dashboards/warehouse/ui/avatar";

export const DashboardHeader = () => {
  return (
    <header
      className="border-b shadow-sm"
      style={{ backgroundColor: "var(--background)", color: "var(--foreground)" }}
    >
      <div className="container mx-auto px-6 py-4">
        <div className="flex items-center justify-between">
          <div>
            <h1
              className="text-3xl font-bold"
              style={{ color: "var(--color-black)" }}
            >
              Warehouse Dashboard
            </h1>
            <p
              className="mt-1"
              style={{ color: "var(--color-dark-teal)" }}
            >
              Welcome back, Manager
            </p>
          </div>

          <div className="flex items-center gap-4">
            <Button
              variant="outline"
              size="icon"
              className="relative"
              style={{
                borderColor: "var(--color-teal)",
                color: "var(--color-teal)",
              }}
            >
              <Bell className="h-4 w-4" />
              <span
                className="absolute -top-1 -right-1 rounded-full text-xs w-5 h-5 flex items-center justify-center"
                style={{
                  backgroundColor: "var(--color-bright-orange)",
                  color: "var(--color-black)",
                }}
              >
                3
              </span>
            </Button>

            <Button
              variant="outline"
              size="icon"
              style={{
                borderColor: "var(--color-teal)",
                color: "var(--color-teal)",
              }}
            >
              <Settings className="h-4 w-4" />
            </Button>

            <Avatar>
              <AvatarImage src="/placeholder.svg" alt="Manager" />
              <AvatarFallback
                style={{ backgroundColor: "var(--color-light-aqua)" }}
              >
                <User
                  className="h-4 w-4"
                  style={{ color: "var(--color-black)" }}
                />
              </AvatarFallback>
            </Avatar>
          </div>
        </div>
      </div>
    </header>
  );
};